import '../ControlPanelStyle.css';

const Dashboard = () => {
  var SD = "12 31 2020";
  var TD = "12 31 2021";
  //var dates = new Date();
  //var TD = ((dates.getMonth() + 1) + " " + dates.getDate() + " " + dates.getFullYear());
  /*var YD = 0;
  for(var i = SDY; i <= TDY; i++){
    if(i % 4 === 0){
      YD = YD + 1;
    }
  }*/
  var date1 = new Date(SD);  
  var date2 = new Date(TD); 
  var time_diff = date2.getTime() - date1.getTime();
  var DD = time_diff / (1000 * 60 * 60 * 24);
  DD = Math.round(DD);
  /*var DPY = 20;
  var WDY = 252;
  var VD = (((DD - (DD % 365)) / 365 ) * DPY) + ((DD % 365) * (WDY / 365) * (DPY / WDY));
  var ttt = (((DD - (DD % 365)) / 365 ) * DPY);*/
  var date = new Date(SD);
  var WE;
  var days = 0;
  for(var i = 0; i <= DD; i++){
    WE = date.getDay();
    console.log(date.getFullYear() + " " + date.getMonth() + " " + date.getDate() + " " + WE);
    if((date.getMonth() === 0)&&(date.getDate() === 1)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 1)&&(date.getDate() === 16)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 2)&&(date.getDate() === 11)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 3)&&(date.getDate() === 1)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 4)&&(date.getDate() === 1)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 5)&&(date.getDate() === 24)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 6)&&(date.getDate() === 6)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 7)&&(date.getDate() === 15)&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 10)&&(date.getDate() === 1 )&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 10)&&(date.getDate() === 2 )&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 11)&&(date.getDate() === 24 )&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 11)&&(date.getDate() === 25 )&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    if((date.getMonth() === 11)&&(date.getDate() === 26 )&&(WE % 6 !== 0)){
      days = days - 0.079365;
    }
    date.setDate(date.getDate() + 1);
  }
  
  /*var date = new Date("04 29 2021");
  date.setDate(date.getDate() + 6);
  var n = date.getDay();*/
  return (
    <div className="dashboard-page">
        <h2>Pradinis</h2>
        <h3>{SD}</h3>
        <h3>{TD}</h3>
        <h3>{DD}</h3>
        <h3>{days}</h3>
    </div>
  );
};

export default Dashboard;