import firebase from 'firebase';
import ControlPanel from '../ControlPanel';
import ReactDOM from 'react-dom';

var EmployeesLogin;
var kodas;
var slaptazodis;
var Data;
var CurrentUser;
export function getLogin(login, typ) {
    return(
        kodas = document.getElementById('code').value,
        slaptazodis = document.getElementById('password').value,
        EmployeesLogin = firebase.database().ref(login).orderByChild(typ).equalTo(kodas),
        EmployeesLogin.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                CurrentUser = childSnapshot.key;
                Data = childSnapshot.val();
            }); 
            if((Data !== undefined)&&(slaptazodis === Data.password)){
                    document.getElementById("login-error").innerHTML = "";
                    ReactDOM.render(null, document.getElementById('header'));
                    ReactDOM.render(null, document.getElementById('footer'));
                    ReactDOM.render(<ControlPanel id={CurrentUser}/>, document.getElementById('root'));
                    if((kodas === Data.adminid)&&(slaptazodis === Data.password)&&(login === "Admin")){
                        document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                        document.getElementById("user-role").innerHTML = "Administratorius";
                        document.getElementById("list-users").disabled = false;
                        document.getElementById("list-users").innerHTML = "Visi vartotojai";
                        document.getElementById("list-mods").disabled = false;
                        document.getElementById("list-mods").innerHTML = "Moderatoriai";
                        document.getElementById("request-vacation").disabled = true;
                        document.getElementById("request-vacation").innerHTML = "disabled";
                        document.getElementById("request-sick").disabled = true;
                        document.getElementById("request-sick").innerHTML = "disabled";
                        document.getElementById("requests-vac").disabled = true;
                        document.getElementById("requests-vac").innerHTML = "disabled";
                    }
                    if((kodas === Data.staffid)&&(slaptazodis === Data.password)&&(Data.role === "buhalteris")){
                        document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                        document.getElementById("user-role").innerHTML = "Buhalteris(-ė)";
                        document.getElementById("list-users").disabled = false;
                        document.getElementById("list-users").innerHTML = "Visi vartotojai";
                        document.getElementById("list-mods").disabled = true;
                        document.getElementById("list-mods").innerHTML = "disabled";
                        document.getElementById("requests-vac").disabled = false;
                        document.getElementById("requests-vac").innerHTML = "Atostogų prašymai";
                        document.getElementById("request-vacation").disabled = true;
                        document.getElementById("request-vacation").innerHTML = "disabled";
                        document.getElementById("request-sick").disabled = true;
                        document.getElementById("request-sick").innerHTML = "disabled";
                    }
                    if((kodas === Data.staffid)&&(slaptazodis === Data.password)&&(Data.role === "vadovas")){
                        document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                        document.getElementById("user-role").innerHTML = "Vadovas(-ė)";
                        document.getElementById("list-users").disabled = true;
                        document.getElementById("list-users").innerHTML = "disabled";
                        document.getElementById("list-mods").disabled = true;
                        document.getElementById("list-mods").innerHTML = "disabled";
                        document.getElementById("requests-vac").disabled = false;
                        document.getElementById("requests-vac").innerHTML = "Atostogų prašymai";
                        document.getElementById("request-vacation").disabled = true;
                        document.getElementById("request-vacation").innerHTML = "disabled";
                        document.getElementById("request-sick").disabled = true;
                        document.getElementById("request-sick").innerHTML = "disabled";
                    }
                    if((kodas === Data.code)&&(slaptazodis === Data.password)&&(login === "User")){
                        document.getElementById("user-name").innerHTML = Data.name + " " + Data.surname;
                        document.getElementById("user-role").innerHTML = "Darbuotojas(-a)";
                        document.getElementById("user-days").innerHTML = "Atostogų dienos: " + Data.days;
                        document.getElementById("list-users").disabled = true;
                        document.getElementById("list-users").innerHTML = "disabled";
                        document.getElementById("list-mods").disabled = true;
                        document.getElementById("list-mods").innerHTML = "disabled";
                        document.getElementById("requests-vac").disabled = true;
                        document.getElementById("requests-vac").innerHTML = "disabled";
                        document.getElementById("request-vacation").disabled = false;
                        document.getElementById("request-vacation").innerHTML = "Atostogų prašymas";
                        document.getElementById("request-sick").disabled = false;
                        document.getElementById("request-sick").innerHTML = "Pranešimas apie ligą";
                    }
                    if(document.getElementById("user-role").innerHTML === "Rolė" ){
                        document.getElementById("list-users").disabled = true;
                        document.getElementById("list-users").innerHTML = "disabled";
                        document.getElementById("list-mods").disabled = true;
                        document.getElementById("list-mods").innerHTML = "disabled";
                        document.getElementById("requests-vac").disabled = true;
                        document.getElementById("requests-vac").innerHTML = "disabled";
                        document.getElementById("request-vacation").disabled = true;
                        document.getElementById("request-vacation").innerHTML = "disabled";
                        document.getElementById("request-sick").disabled = true;
                        document.getElementById("request-sick").innerHTML = "disabled";
                    }
            }            
            else document.getElementById("login-error").innerHTML = "Blogi prisijungimo duomenys";
        })
    );  
};