import firebase from 'firebase';
import '../ControlPanelStyle.css';

var GetUser;
var VacationAdd;
var Data1;
var Data2;
var Vacation;
var vardas;
var pavarde;
var check;
var imone;
var dienos;
export function AddVacation(UID, type, today, SD, ED, duration){
    check = 0;
    return(
        GetUser = firebase.database().ref("User").orderByKey().equalTo(UID),
        GetUser.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                Data1 = childSnapshot.val();
            });
            vardas = Data1.name;
            pavarde = Data1.surname;
            imone = Data1.company;
            dienos = Data1.days 
            VacationAdd = firebase.database().ref("Vacations");
            VacationAdd.once('value', (snapshot) => {
                snapshot.forEach((childSnapshot) => {
                    Data2 = childSnapshot.val();
                    if((UID === Data2.user)&&(Data2.status !== 3)){
                        check = 1;
                        document.getElementById("vacation-result").innerHTML = "Jūs jau esate pateikę atostogų prašymą";
                        document.getElementById("vacation-result").style.color = "red";
                    }
                });
                if((check === 0)&&(dienos >= duration)&&(type === "Kasmetinės atostogos")){
                    Vacation = VacationAdd.push();
                    Vacation.set({
                        id: Data2.id + 1,
                        user: UID,
                        name: vardas,
                        surname: pavarde,
                        type: type,
                        date: today,
                        startDate: SD,
                        endDate: ED,
                        status: 1,
                        company: imone,
                        duration: duration,
                    });
                    sendEmail(vardas, pavarde, type);
                    document.getElementById("vacation-result").innerHTML = "Prašymas sėkmingai išsiųstas";
                    document.getElementById("vacation-result").style.color = "green";
                }
                else{
                    document.getElementById("vacation-result").innerHTML = "Nepakanka atostoginių dienų.<br/>Turimos dienos: " + dienos + "</br>Pasirinktų atostogų trukmė dienomis: " + duration;
                    document.getElementById("vacation-result").style.color = "red";
                }
                if((check === 0)&&(dienos >= duration)&&(type === "Neapmokamos atostogos")){
                    Vacation = VacationAdd.push();
                    Vacation.set({
                        id: Data2.id + 1,
                        user: UID,
                        name: vardas,
                        surname: pavarde,
                        type: type,
                        date: today,
                        startDate: SD,
                        endDate: ED,
                        status: 1,
                        company: imone,
                        duration: duration,
                    });
                    sendEmail(vardas, pavarde, type);
                    document.getElementById("vacation-result").innerHTML = "Prašymas sėkmingai išsiųstas";
                    document.getElementById("vacation-result").style.color = "green";
                }
            })
        })
    );
}

function sendEmail(vardas, pavarde, type) {
    if(type === "Kasmetinės atostogos"){type = "kasmetinių atostogų"}
    if(type === "Neapmokamos atostogos"){type = "neapmokamų atostogų"}
    if(type === "Kitos atostogos"){type = "kitų atostogų"}
    window.Email.send({
      Host: "smtp.gmail.com",
      Username: "linas.semiotas.test@gmail.com",
      Password: "mailertest",
      To: 'linas.semiotas.test@gmail.com',
      From: "linas.semiotas.test@gmail.com",
      Subject: "Atostogų prašymas",
      Body: "Darbuotojas " + vardas + " " + pavarde + " nori išeiti " + type + ". Prašome patikrinti programėlę"
    })
      .then(function (message) {
        alert("Prašymas sėkmingai išsiųstas")
    });
}