import firebase from 'firebase';
import '../ControlPanelStyle.css';

var check;
var AddData;
var Auid;
var Duid;
var Uuid;
var AddTableData;
var DelTableData;
var UpdTableData;
export function TableActionAdd(name, surname, email, days, code, company) {
  check = 0;
  return (
    AddTableData = firebase.database().ref("User"),
    AddTableData.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        AddData = childSnapshot.val();
        Auid = AddData.id;
        if(AddData.code === code){
          check = 1;
        }
      });
      if(check === 0){
        var addUser = firebase.database().ref("User");
        var addNewUser = addUser.push();
        addNewUser.set({
          id: (Auid + 1),
          name: name,
          surname: surname,
          email: email,
          days: days,
          code: code,
          password: "",
          company: company
        })
      }

    })
  );
}

export function TableActionDel(delindex) {
  return (
    DelTableData = firebase.database().ref("User").orderByChild("id").equalTo(delindex),
    DelTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Duid = childSnapshot.key;  
        });
        var delUser = firebase.database().ref("User/" + Duid);
        delUser.remove(); 
    })
  );
}
export function TableActionUpd(updindex, name, surname, email, days, company) {

  return (
    UpdTableData = firebase.database().ref("User").orderByChild("id").equalTo(updindex),
    UpdTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Uuid = childSnapshot.key;
        });
        var updUser = firebase.database().ref("User/" + Uuid);
        updUser.update({
          name: name,
          surname: surname,
          email: email,
          days: days,
          company: company
        }); 
    })
  );
}